@font-face {
  font-family: "Bellerose";
  src: url('../ressources/Bellerose.ttf') format('truetype');
}

@font-face {
  font-family: 'Cormorant';
  src: url('../ressources/CormorantGaramond-Light.ttf') format('truetype');
  font-weight: lighter;
  font-style: normal;
  font-display: swap;
}

@mixin phone {
  @media (max-width: 600px) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: 800px) {
    @content;
  }
}

@mixin desktop--S {
  @media (max-width: 1200px) {
    @content;
  }
}

@mixin desktop {
  @media (max-width: 1920px) {
    @content;
  }
}

@mixin large {
  @media (min-width: 1920px) {
    @content;
  }
}

@import './first_section.sass';
@import './normalize.css';
@import './second_section.scss';

html {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

body {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: linear-gradient(#D1FEF6, #FDEB9B 10%);
}

body::before {
  content: '';
  display: inline-block;
  width: 300vw;
  height: 300vh;
  position: absolute;
  top: calc(50% - 150vh);
  left: calc(50% - 150vw);
  transition: all 0.5s ease-in-out;
  background-image: linear-gradient(#D1FEF6 30%, #FDEB9B 40%, #3568b6 70%);
  transform-origin: center;
}

.body--pink::before {
  transform: translateY(-100vh);
}

